// start

import './input.scss'
// end

import React,{Component, propTypes } from "react" ;

import {Router , Route, Link , hashHistory} from "react-router";
class Input extends Component{
    constructor(){
        super();
    };
    componentWillMount(){
        this.state={
            stErr:'none',
            stNull:"none",
            password:'none',
            passNull:'none',
            passLength:'none',
            rule:'none',
            pasSucs:false,
            useSucs:false
        }
    };
    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
        /*this.handFocus();*/
    };
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
        //  end
    };
    handBlur(){
        let phone= this.refs.phone.value;
        let reg=new RegExp(/^0?1[3|4|5|7|8][0-9]\d{8}$/);
        let phones=phone.length;

        let tel=phone.match(reg)
       if(phone==''){
            this.setState({stNull:"block"})
           this.setState({useSucs:false})
        }else{
           if(phones!=11){
               this.setState({stErr:"block"});
               this.setState({useSucs:false})
           }else if(tel){
               /*console.log("");*/
               this.setState({stErr:"none"})
               this.setState({useSucs:true})
           }else{
               /*console.log("")*/
               this.setState({stErr:"block"})
               this.setState({useSucs:false})
           }
       };
    };
    handPass(){
        let patrn=new RegExp(/^(\w){6,20}$/);

        let pass=this.refs.password.value;
        let pw=pass.match(patrn );

        console.log("pw:"+pw)
        if(pass==""){
            this.setState({password:"block"})
            this.setState({pasSucs:false})
        }else{
            if(pass.length<6 || pass.length>20){
                this.setState({passLength:"block"})
                this.setState({pasSucs:false})
            }else{
                if(pw===null){
                    this.setState({rule:'block'})
                    this.setState({pasSucs:false})
                }else{
                    this.setState({pasSucs:true})
                }
            }
        }
    };
    handFocus(){
        this.setState({stErr:"none"})
        this.setState({stNull:"none"})
    };
    handPassFocus(){
        this.setState({passLength:"none"})
        this.setState({password:"none"})
        this.setState({rule:"none"})
    };
    btnClick(){
        if(this.state.useSucs && this.state.pasSucs){
            alert("")
        }else{
            alert('')
        }
    }
    render(){
        return(
            <div>
                <div className="input">
                    <div>
                        <span></span><input type="number" onBlur={this.handBlur.bind(this)} onFocus={this.handFocus.bind(this)}  ref="phone" placeholder="" />
                        <p className="user" style={{display:this.state.stErr}}>*</p>
                        <p className="user" style={{display:this.state.stNull}}></p>
                    </div>

                    <div>
                        <span></span><input type="password" onFocus={this.handPassFocus.bind(this)} onBlur={this.handPass.bind(this)} ref="password" placeholder="" />
                        <p className="pass" style={{display:this.state.password}}>*</p>
                        <p className="pass" style={{display:this.state.passLength}}>*6-20</p>
                        <p className="pass" style={{display:this.state.rule}}>*6-20</p>
                    </div>
                    <div><div className="divBtn" onClick={this.btnClick.bind(this)} ></div></div>
                </div>
            </div>
        )
    }
}
export default Input;


